.Card-line {
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-name: slide-fade;
          animation-name: slide-fade;
}

.Card-line:first-letter {
  text-transform: capitalize;
}

.Card-indent {
  margin-left: 2em;
}

.Card-noindent {
  margin-right: 2em;
}

div.Card-theme {
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}

.Card-theme,
.CardText-strong,
.Card-strong {
  color: white;
  text-shadow: 0px 0px 6px white;
}



