.Card-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 0px;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  width: 80%;
}

.Card {
  min-height: 200px;
  max-height: 600px;
  background-color: rgba(0, 0, 0, 0.75);
  color: #ddd;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding-bottom: 0px;
  -webkit-box-shadow: 0px 0px 110px 80px rgba(0, 0, 0, 0.75);
          box-shadow: 0px 0px 110px 80px rgba(0, 0, 0, 0.75);
  border-radius: 40px;
  /*border: 20px solid rgba(0,0,0,0);*/
  padding-left: 0px;
  opacity: 1;
  z-index: 2;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  -webkit-animation-name: slide-fade;
          animation-name: slide-fade;
}

@media (min-width: 858px) {
    .Card-container {
      max-width: 700px;
    }
}

.Card-fade-in {
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-name: slide-fade;
          animation-name: slide-fade;
  opacity: 1;
}

@-webkit-keyframes slide-fade {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes slide-fade {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.Card-fade-out {
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-name: slide-fade-out;
          animation-name: slide-fade-out;
  opacity: 0;
}

@-webkit-keyframes slide-fade-out {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@keyframes slide-fade-out {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

.Card div {
  margin-top: 30px;
}



